<script>
import * as echarts from 'echarts'
import axios from 'axios'
import AddOrUpdate from "./add-or-update";
export default {
  data() {
    return {
      jingsaimingchengOptions: [],
      xuehaoOptions: [],
      searchForm: {
        key: ""
      },
      form:{},
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      chartVisiable1: false,
      chartVisiable2: false,
      chartVisiable3: false,
      chartVisiable4: false,
      chartVisiable5: false,
      addOrUpdateFlag:false,
      layouts: ["total","prev","pager","next","sizes","jumper"],

    };
  },
  created() {
    this.init();
    this.getDataList();
    this.contentStyleChange()
  },
  mounted() {
  },
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
    }
  },
  components: {
    AddOrUpdate,
  },
  methods: {

    contentStyleChange() {
      this.contentPageStyleChange()
    },
    // 分页
    contentPageStyleChange(){
      let arr = []

      // if(this.contents.pageTotal) arr.push('total')
      // if(this.contents.pageSizes) arr.push('sizes')
      // if(this.contents.pagePrevNext){
      //   arr.push('prev')
      //   if(this.contents.pagePager) arr.push('pager')
      //   arr.push('next')
      // }
      // if(this.contents.pageJumper) arr.push('jumper')
      // this.layouts = arr.join()
      // this.contents.pageEachNum = 10
    },



//统计接口
    chartDialog1() {
      this.chartVisiable1 = !this.chartVisiable1;
      this.$nextTick(()=>{

        var jingsaichengjiChart1 = echarts.init(document.getElementById("jingsaichengjiChart1"),'macarons');
        this.$http({
            url: "jingsaichengji/sectionStat/jingsaichengji/jingsaimingcheng",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis1 = [];
                let yAxis2 = [];
                let yAxis3 = [];
                let yAxis4 = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].jingsaimingcheng);
                    yAxis1.push(parseFloat((res[i].不及格)));
                    yAxis2.push(parseFloat((res[i].及格)));
                    yAxis3.push(parseFloat((res[i].良)));
                    yAxis4.push(parseFloat((res[i].优秀)));
                    pArray.push({
                        name: res[i].jingsaichengji
                    })
                }
                var option = {};
                option = {
                    title: {
                        text: '竞赛成绩分析',
                        left: 'center'
                    },
                    legend: {
                      data: [
                        '不及格',
                        '及格',
                        '良',
                        '优秀',
                        ],
                      left: 'center',
                      bottom: 4,
                    },
                    tooltip: {
                      trigger: 'axis'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                    {
                        name: '不及格',
                        data: yAxis1,
                        type: 'bar'
                    },
                    {
                        name: '及格',
                        data: yAxis2,
                        type: 'bar'
                    },
                    {
                        name: '良',
                        data: yAxis3,
                        type: 'bar'
                    },
                    {
                        name: '优秀',
                        data: yAxis4,
                        type: 'bar'
                    },
                    ],
                };
                // 使用刚指定的配置项和数据显示图表。
                jingsaichengjiChart1.setOption(option);
                  //根据窗口的大小变动图表
                window.onresize = function() {
                    jingsaichengjiChart1.resize();
                };
            }
        });
      })
    },





    init () {
          this.$http({
            url: `option/jingsaixinxi/jingsaimingcheng`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.jingsaimingchengOptions = data.data;
            } else {
              this.$message.error(data.msg);
            }
          });
          this.$http({
            url: `option/xuesheng/xuehao`,
            method: "get"
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.xuehaoOptions = data.data;
            } else {
              this.$message.error(data.msg);
            }
          });
    },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },

    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
        order: 'desc',
      }
           if(this.searchForm.jingsaimingcheng!='' && this.searchForm.jingsaimingcheng!=undefined){
            params['jingsaimingcheng'] = this.searchForm.jingsaimingcheng
          }
           if(this.searchForm.xuehao!='' && this.searchForm.xuehao!=undefined){
            params['xuehao'] = this.searchForm.xuehao
          }
      this.$http({
        url: "jingsaichengji/page",
        method: "get",
        params: params
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 多选
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // 添加/修改
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
    // 下载
    download(file){
      window.open(`${file}`)
    },
    // 删除
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`确定进行[${id ? "删除" : "批量删除"}]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "jingsaichengji/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "操作成功",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },


  }

};
</script>
